import { CellControllerManager } from '../table.cell_controller_manager';
import { AbstractCellHandler } from './handler.abstract';

export class WidgetActiveHandler extends AbstractCellHandler {
    public eventNames = ['CELL_DOUBLE_CLICK'];

    public name = 'CELL_ACTIVE_WIDGET';

    public merger = 'covered';

    public async action({ id }: Parameters<AbstractCellHandler['action']>[0] & { id: string }) {
        const cellControllerManager = <CellControllerManager | undefined>(this.externalContext.getStickerManagers().get(CellControllerManager.NAME));


        if (!cellControllerManager) {
            return;
        }

        cellControllerManager.getWidgetsMap()
            .forEach((controller, name) => {
                name === id && controller.active();
            });
    }
}
